<template>
	<div>
		<el-container>
			<!--显示歌曲信息列表-->
			<el-main>
				<el-table stripe :data="MusicListDetails"
				          style="cursor: pointer;">
					<el-table-column label="" type="index" width="50px;"></el-table-column>
				    <el-table-column label="歌曲" prop="name" width="300px;">
						<template slot-scope="scope">
							<span @click="toLyricPage($store.state.ids[scope.$index])">{{MusicListDetails[scope.$index].name}}</span>
						</template>
					</el-table-column>
				    <el-table-column label="歌手" prop="ar[0].name" width="200px;"></el-table-column>
				    <el-table-column label="专辑名" prop="al.name" width="300px;"></el-table-column>
				    <el-table-column label="时长" prop="dt" width="80px;">
						<template slot-scope="scope">
							<span>{{scope.row.dt/1000 | timeFormat("")}}</span>
						</template>
					</el-table-column>
					<el-table-column width="75px;">
						<template slot-scope="scope">
							<span v-if="scope.row.mv=='' || scope.row.mv==null">
								<i class="el-icon-video-play" @click="playMusic($store.state.ids[scope.$index])"></i>
							</span>
							<span v-else="">
								<i class="el-icon-video-play" @click="playMusic($store.state.ids[scope.$index])"></i>
								<i style="margin-left:10px;" class="el-icon-video-camera" @click="playMv(scope.row.mv)"></i>
							</span>
						</template>
					</el-table-column>
				</el-table>
			</el-main>
			<el-footer class="el-footer">
				<div class="img" style="width: 100%;"></div>
				<a href="#">
					<div class="mask">
						<audio style="width: 100%;" :src="musicUrl" controls="controls" autoplay="autoplay" @play="play"
							@pause="pause"></audio>
					</div>
				</a>
			</el-footer>
		</el-container>
		<!--视频播放-->
		<div class="playmv" v-show="isPlayMv">
			<i class="el-icon-close" @click="hide"></i>
			<video class="center-block" :src="mvUrl" controls="controls" autoplay="autoplay"></video>
		</div>
	</div>
</template>

<script>
    export default {
        data() {
            return {
                //根据tracksIds中的id获取所有的歌曲详情
                MusicListDetails: [],	
				//歌曲的URL
				musicUrl:"",
				//是否播放
				isPlaying:false,
				//MV地址
				mvUrl:"",
				//MV播放
				isPlayMv:false
            }
        },
        created() {
            //获取歌曲详情
            this.getSongList()
        },
        methods: {
            //根据id获取对应歌单所有歌曲的详情
			//排行榜接口不可用了,因此使用歌单详情接口获取trackIds,再根据trackIds中的id去获取歌曲详情
            getSongList() {
				//store中的trackIds
				var trackIds=this.$store.state.trackIds;
				var ids=[];
				//将trackIds中的所有id值拿出来,放在ids数组中
				//遍历trackIds,将id拿出来放入ids数组中
				for(var i=0;i<trackIds.length;i++){
					ids[i]=trackIds[i].id;
				}
				//将ids存入store中
				this.$store.commit("addIds",ids);
				//根据ids获取对应歌曲的详细信息
                this.$axios.get("https://autumnfish.cn/song/detail?ids="+ids)
				.then((res) => {
                    this.MusicListDetails = res.data.songs;   
                })
            },
			toLyricPage(songIndex){
				//将点击歌曲的对应id值存入store中,用于查找对应歌曲的歌词等详细信息
				this.$store.commit("addSongIndex",songIndex);
				this.$router.push({
					name:"lyric",
				});
			},
			playMusic(id){
				var that=this;
				//获取歌曲链接
				this.$axios.get("https://autumnfish.cn/song/url?id="+id)
				.then((response)=>{
					that.musicUrl=response.data.data[0].url;
				},(err)=>{})
			},
			play(){
				this.isPlaying=true;
			},
			pause(){
				this.isPlaying=false;
			},
			//获取视频资源
			playMv(mvid){
				var that=this;
				this.$axios.get("https://autumnfish.cn/mv/url?id="+mvid)
				.then((response)=>{
					that.isPlayMv=true;
					that.mvUrl=response.data.data.url;
				},(err)=>{})
			},
			hide(){
				this.isPlayMv=false;
			},
        }
    }
</script>

<style scoped>
.el-table-column--selection:hover{
	color: skyblue;
}
audio{
	width: 100%;
	margin-left: -55px;
}
.el-footer{
	width: 100%;
	position: fixed;
	margin-top: 150px;
	color: black;
	text-align: center;
}
.mask{
	position: absolute;
	width: 85%;
	opacity: 0;
	margin-right: 50px;
}
.mask audio{
	text-align: center;
}
.el-footer a:hover .mask{
	opacity: 1;
}	
.el-icon-close{
	margin-top: 55px;
	float: left;
	font-size: 35px;
	margin-top: 10px;
	margin-left: 96%;
	color: white;
}
.playmv{
	float: left;
	width: 100%;
	height: 780px;
	position: fixed;
	z-index: 10;
	background-color: rgba(0,0,0,0.6);
	top: 0;
	left: 0;	
}
.playmv video{
	width: 50%;
	height: 500px;
	margin-left: 320px;
}
</style>
